<template>
  <div class="profile-container">
    <div class="profile-header">
      <div class="avatar-section">
        <div class="avatar" v-if="avatar_url">
          <img :src="avatar_url" :alt="username">
        </div>
        <div class="avatar placeholder" v-else>
          <span class="material-symbols-rounded">person</span>
        </div>
        <button class="change-avatar" @click="triggerImageUpload">
          <span class="material-symbols-rounded">add_a_photo</span>
        </button>
        <input 
          type="file" 
          ref="fileInput" 
          style="display: none" 
          accept="image/*"
          @change="handleImageUpload"
        >
      </div>
      
      <div class="profile-info">
        <h1>{{ username || email }}</h1>
        <p class="join-date">Member since {{ formatDate(created_at) }}</p>
      </div>
    </div>

    <div class="profile-content">
      <div class="profile-section">
        <h2>Personal Information</h2>
        <form @submit.prevent="updateProfile" class="profile-form">
          <div class="form-group">
            <label>First Name</label>
            <div class="input-wrapper">
              <span class="material-symbols-rounded">person</span>
              <input v-model="formData.firstName" type="text" placeholder="Your first name">
            </div>
          </div>

          <div class="form-group">
            <label>Last Name</label>
            <div class="input-wrapper">
              <span class="material-symbols-rounded">person</span>
              <input v-model="formData.lastName" type="text" placeholder="Your last name">
            </div>
          </div>

          <div class="form-group">
            <label>Username</label>
            <div class="input-wrapper">
              <span class="material-symbols-rounded">badge</span>
              <input v-model="formData.username" type="text" placeholder="Choose a username">
            </div>
          </div>

          <div class="form-group">
            <label>Email</label>
            <div class="input-wrapper">
              <span class="material-symbols-rounded">mail</span>
              <input :value="email" type="email" disabled>
            </div>
          </div>

          <button type="submit" :disabled="loading" class="submit-button">
            <span class="material-symbols-rounded">save</span>
            Save Changes
          </button>
        </form>
      </div>

      <div class="profile-section">
        <h2>Collection Overview</h2>
        <div class="stats-grid">
          <div class="stat-card">
            <span class="material-symbols-rounded">style</span>
            <div class="stat-info">
              <span class="stat-value">{{ stats.total_cards || 0 }}</span>
              <span class="stat-label">Total Cards</span>
            </div>
          </div>
          <div class="stat-card">
            <span class="material-symbols-rounded">diamond</span>
            <div class="stat-info">
              <span class="stat-value">${{ formatPrice(stats.total_value) }}</span>
              <span class="stat-label">Collection Value</span>
            </div>
          </div>
          <div class="stat-card">
            <span class="material-symbols-rounded">category</span>
            <div class="stat-info">
              <span class="stat-value">{{ stats.unique_cards || 0 }}</span>
              <span class="stat-label">Unique Cards</span>
            </div>
          </div>
        </div>
      </div>

      <div class="profile-section">
        <h2>Notification Settings</h2>
        <div class="settings-list">
          <div class="setting-item">
            <div class="setting-info">
              <h3>Price Alerts</h3>
              <p>Get notified when cards in your collection change in value</p>
            </div>
            <label class="toggle">
              <input type="checkbox" v-model="notifications.priceAlerts">
              <span class="toggle-slider"></span>
            </label>
          </div>

          <div class="setting-item">
            <div class="setting-info">
              <h3>Collection Updates</h3>
              <p>Notifications about your collection statistics</p>
            </div>
            <label class="toggle">
              <input type="checkbox" v-model="notifications.collectionUpdates">
              <span class="toggle-slider"></span>
            </label>
          </div>

          <div class="setting-item">
            <div class="setting-info">
              <h3>Market Trends</h3>
              <p>Weekly market analysis and trending cards</p>
            </div>
            <label class="toggle">
              <input type="checkbox" v-model="notifications.marketTrends">
              <span class="toggle-slider"></span>
            </label>
          </div>
        </div>
      </div>

      <div class="profile-section danger-zone">
        <h2>Account Settings</h2>
        <div class="danger-actions">
          <div class="danger-item">
            <div class="danger-info">
              <h3>Change Password</h3>
              <p>Update your account password</p>
            </div>
            <button class="danger-button secondary" @click="showChangePasswordModal = true">
              <span class="material-symbols-rounded">lock</span>
              Change Password
            </button>
          </div>

          <div class="danger-item">
            <div class="danger-info">
              <h3>Delete Account</h3>
              <p>Permanently delete your account and data</p>
            </div>
            <button class="danger-button" @click="showDeleteModal = true">
              <span class="material-symbols-rounded">delete</span>
              Delete Account
            </button>
          </div>
        </div>
      </div>
    </div>

    <Modal v-if="showChangePasswordModal" @close="showChangePasswordModal = false">
      <!-- password change form -->
    </Modal>

    <Modal v-if="showDeleteModal" @close="showDeleteModal = false">
      <!-- delete confirmation -->
    </Modal>
  </div>
</template>

<script>
import { ref, onMounted } from 'vue'
import { supabase } from '@/services/supabase'
import auth from '@/store/auth'

export default {
  name: 'Profile',
  setup() {
    const fileInput = ref(null)
    const loading = ref(false)
    const error = ref(null)
    const avatar_url = ref(null)
    const username = ref('')
    const email = ref('')
    const created_at = ref(null)
    const stats = ref({})
    
    const formData = ref({
      firstName: '',
      lastName: '',
      username: ''
    })

    const notifications = ref({
      priceAlerts: true,
      collectionUpdates: true,
      marketTrends: false
    })

    const showChangePasswordModal = ref(false)
    const showDeleteModal = ref(false)

    const loadProfile = async () => {
      try {
        const { data, error: profileError } = await supabase
          .from('profiles')
          .select('*')
          .eq('id', auth.state.user.id)
          .single()

        if (profileError) throw profileError

        username.value = data.username
        avatar_url.value = data.avatar_url
        created_at.value = data.created_at
        email.value = auth.state.user.email
        formData.value = {
          username: data.username,
          firstName: data.first_name || '',
          lastName: data.last_name || ''
        }

        // Load stats
        const { data: statsData } = await supabase
          .rpc('get_collection_stats', { user_id: auth.state.user.id })
        
        stats.value = statsData[0]
      } catch (err) {
        console.error('Error loading profile:', err)
      }
    }

    const updateProfile = async () => {
      loading.value = true
      error.value = null

      try {
        const { error: updateError } = await supabase
          .from('profiles')
          .update({
            username: formData.value.username,
            first_name: formData.value.firstName,
            last_name: formData.value.lastName,
            updated_at: new Date()
          })
          .eq('id', auth.state.user.id)

        if (updateError) throw updateError
        
        // Reload user profile in auth store
        await auth.loadUserProfile()
        await loadProfile()
      } catch (err) {
        error.value = err.message
      } finally {
        loading.value = false
      }
    }

    const handleImageUpload = async (event) => {
      try {
        loading.value = true
        const file = event.target.files[0]
        const fileExt = file.name.split('.').pop()
        const filePath = `${auth.state.user.id}/avatar.${fileExt}`

        const { error: uploadError } = await supabase.storage
          .from('avatars')
          .upload(filePath, file, { upsert: true })

        if (uploadError) throw uploadError

        const { data: { publicUrl } } = supabase.storage
          .from('avatars')
          .getPublicUrl(filePath)

        await supabase
          .from('profiles')
          .update({ avatar_url: publicUrl })
          .eq('id', auth.state.user.id)

        avatar_url.value = publicUrl
      } catch (err) {
        console.error('Error uploading image:', err)
      } finally {
        loading.value = false
      }
    }

    const triggerImageUpload = () => {
      fileInput.value.click()
    }

    const formatDate = (date) => {
      return new Date(date).toLocaleDateString('en-US', {
        month: 'long',
        year: 'numeric'
      })
    }

    const formatPrice = (price) => {
      return Number(price || 0).toFixed(2)
    }

    onMounted(() => {
      loadProfile()
    })

    return {
      fileInput,
      loading,
      error,
      avatar_url,
      username,
      email,
      created_at,
      formData,
      stats,
      notifications,
      showChangePasswordModal,
      showDeleteModal,
      updateProfile,
      handleImageUpload,
      triggerImageUpload,
      formatDate,
      formatPrice
    }
  }
}
</script>

<style scoped>
.profile-container {
  max-width: 1000px;
  margin: 40px auto;
  padding: 0 24px;
}

.profile-header {
  display: flex;
  align-items: center;
  gap: 32px;
  margin-bottom: 40px;
}

.avatar-section {
  position: relative;
}

.avatar, .avatar.placeholder {
  width: 120px;
  height: 120px;
  border-radius: 60px;
  background: var(--card-background);
  display: flex;
  align-items: center;
  justify-content: center;
  overflow: hidden;
}

.avatar img {
  width: 100%;
  height: 100%;
  object-fit: cover;
}

.avatar.placeholder .material-symbols-rounded {
  font-size: 48px;
  color: var(--text-secondary);
}

.change-avatar {
  position: absolute;
  bottom: 0;
  right: 0;
  width: 36px;
  height: 36px;
  border-radius: 50%;
  background: var(--accent-primary);
  border: none;
  color: white;
  display: flex;
  align-items: center;
  justify-content: center;
  cursor: pointer;
  box-shadow: var(--shadow-md);
}

.profile-info h1 {
  margin: 0;
  font-size: 32px;
}

.join-date {
  color: var(--text-secondary);
  margin: 8px 0 0 0;
}

.profile-content {
  display: grid;
  gap: 32px;
}

.profile-section {
  background: var(--card-background);
  border-radius: 16px;
  padding: 24px;
  border: 1px solid var(--card-border);
}

.profile-section h2 {
  margin: 0 0 24px 0;
  font-size: 20px;
}

.profile-form {
  max-width: 400px;
}

.form-group {
  margin-bottom: 20px;
}

.form-group label {
  display: block;
  margin-bottom: 8px;
  color: var(--text-secondary);
  font-size: 14px;
}

.input-wrapper {
  display: flex;
  align-items: center;
  background: var(--surface-background);
  border-radius: 12px;
  padding: 12px 16px;
  transition: all 0.2s ease;
}

.input-wrapper .material-symbols-rounded {
  color: var(--text-secondary);
  margin-right: 12px;
}

.input-wrapper input {
  width: 100%;
  background: none;
  border: none;
  outline: none;
  color: var(--text-primary);
  font-size: 15px;
}

.stats-grid {
  display: grid;
  grid-template-columns: repeat(auto-fit, minmax(200px, 1fr));
  gap: 16px;
}

.stat-card {
  display: flex;
  align-items: center;
  gap: 16px;
  padding: 20px;
  background: var(--surface-background);
  border-radius: 12px;
}

.stat-card .material-symbols-rounded {
  font-size: 32px;
  color: var(--accent-primary);
}

.stat-info {
  display: flex;
  flex-direction: column;
}

.stat-value {
  font-size: 24px;
  font-weight: 600;
}

.stat-label {
  font-size: 14px;
  color: var(--text-secondary);
}

.submit-button {
  display: flex;
  align-items: center;
  justify-content: center;
  gap: 8px;
  width: 100%;
  padding: 14px;
  background: var(--accent-primary);
  color: white;
  border: none;
  border-radius: 12px;
  font-size: 15px;
  font-weight: 600;
  cursor: pointer;
}

.error-message {
  display: flex;
  align-items: center;
  gap: 8px;
  padding: 12px;
  background: var(--system-red);
  color: white;
  border-radius: 8px;
  margin-bottom: 16px;
  font-size: 14px;
}

.settings-list {
  display: flex;
  flex-direction: column;
  gap: 16px;
}

.setting-item {
  display: flex;
  justify-content: space-between;
  align-items: center;
  padding: 16px;
  background: var(--surface-background);
  border-radius: 12px;
}

.setting-info h3 {
  margin: 0;
  font-size: 16px;
}

.setting-info p {
  margin: 4px 0 0 0;
  font-size: 14px;
  color: var(--text-secondary);
}

.toggle {
  position: relative;
  display: inline-block;
  width: 52px;
  height: 28px;
}

.toggle input {
  opacity: 0;
  width: 0;
  height: 0;
}

.toggle-slider {
  position: absolute;
  cursor: pointer;
  top: 0;
  left: 0;
  right: 0;
  bottom: 0;
  background-color: var(--background-elevated);
  transition: .4s;
  border-radius: 34px;
}

.toggle-slider:before {
  position: absolute;
  content: "";
  height: 20px;
  width: 20px;
  left: 4px;
  bottom: 4px;
  background-color: white;
  transition: .4s;
  border-radius: 50%;
}

input:checked + .toggle-slider {
  background-color: var(--accent-primary);
}

input:checked + .toggle-slider:before {
  transform: translateX(24px);
}

.danger-zone {
  border-color: var(--system-red);
}

.danger-actions {
  display: flex;
  flex-direction: column;
  gap: 16px;
}

.danger-item {
  display: flex;
  justify-content: space-between;
  align-items: center;
  padding: 16px;
  background: var(--surface-background);
  border-radius: 12px;
}

.danger-button {
  display: flex;
  align-items: center;
  gap: 8px;
  padding: 8px 16px;
  border-radius: 8px;
  border: none;
  font-size: 14px;
  font-weight: 500;
  cursor: pointer;
  background: var(--system-red);
  color: white;
}

.danger-button.secondary {
  background: var(--surface-background);
  color: var(--text-primary);
}

@media (max-width: 640px) {
  .profile-header {
    flex-direction: column;
    text-align: center;
  }

  .profile-info h1 {
    font-size: 24px;
  }
}
</style>
